import React from 'react'
import './header.css'

export default class Header extends React.Component {
  constructor (props) {
    super(props)

    this.state = {
      height: 0
    }
  }

  componentDidMount () {
    // –ø–æ–ª—É—á–∞–µ–º –≤—ã—Å–æ—Ç—É —Ö–µ–¥–µ—Ä–∞
    const height = document.querySelector('.View__header')
    // –µ—Å–ª–∏ –æ–Ω–∞ –ø–æ–ª—É—á–µ–Ω–∞ —Ç–æ —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –µ–µ
    if (height) {
      this.setState({ height: height.clientHeight })
    }
  }

  render () {
    return (
      <div className='header'>
        <div className='header__header-ellipse' />
      </div>
    )
  }
}
