import ax from './axios'
import API from './API'

const reg = async (id, fullName) => {
  // –Ω—É–∂–Ω–æ —á—Ç–æ–±—ã –≤ –Ω–µ–µ –¥–æ–±–∞–≤–ª—è—Ç—å —Å—Ä–∞–∑—É –≤—Å–µ —á—Ç–æ –Ω—É–∂–Ω–æ –ø–µ—Ä–µ–¥–∞—Ç—å
  const params = new URLSearchParams()

  // –í –¥–∞–Ω–Ω–æ–º —Å–ª—É—á–∞–µ —ç—Ç–æ –∞–π–¥–∏: —Å–ª–µ–≤–∞ –Ω–∞–∏–º–µ–Ω–æ–≤–∞–Ω–∏–µ - —Å–ø—Ä–∞–≤–∞ –∑–Ω–∞—á–µ–Ω–∏–µ
  params.append('id', id)
  params.append('full_name', fullName)

  // –ø–æ—Å—ã–ª–∞–µ–º –¥–∞–Ω–Ω—ã–µ –Ω–∞ url –Ω–∏–∂–µ, –ø–µ—Ä–µ–¥–∞–µ–º –Ω–∞—à–∏ –ø–∞—Ä–∞–º–µ—Ç—Ä—ã, —É–∫–∞–∑—ã–≤–∞–µ–º –∑–∞–≥–æ–ª–æ–≤–∫–∏
  try {
    const response = await ax.userRegister(params)

    // –ø–æ–∫–∞–∑–∞—Ç—å —á—Ç–æ –ø—Ä–∏—à–ª–æ
    console.log('customers.reg', response)
    if (response.data.role === 'ADMIN') {
      sessionStorage.setItem('role', 'ADMIN');
    }
    // –ï—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ –ø–æ–¥–ø–∏—Å–∞–Ω –Ω–∞ —Å–æ–æ–±—â–µ—Å—Ç–≤–æ, —Ç–æ –∑–∞–ø—Ä–æ—Å–∏—Ç—å —á—Ç–æ–±—ã –ø–æ–¥–ø–∏—Å–∞–ª—Å—è
    if (response.data.hasOwnProperty('isMember')) {
      if (response.data.isMember === 0) {
        API.joinGroup()
      }
    }

    // –ï—Å–ª–∏ —É–≤–µ–¥–æ–º–ª–µ–Ω–∏—è –∑–∞–ø—Ä–µ—â–µ–Ω—ã, —Ç–æ –∑–∞–ø—Ä–æ—Å–∏—Ç—å —Ä–∞–∑—Ä–µ—à–µ–Ω–∏–µ –Ω–∞ –ø–æ–ª—É—á–µ–Ω–∏–µ —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–π
    if (response.data.hasOwnProperty('isNotificationsAllowed')) {
      if (response.data.isNotificationsAllowed === 0) {
        API.allowNotifications()
      }
    }

    // –µ—Å–ª–∏ –æ—à–∏–±–∫–∞ –∏ –ø—Ä–∏—à–µ–ª —Å—Ç–∞—Ç—É—Å –µ–µ
    if (response.data.hasOwnProperty('status')) {
      // alert(response.data.status)
      console.log(response.data.status)
    }
  } catch (error) {
    console.log(error)
  }
}

export default reg
