import React from 'react'

import './portfolio.css'

import Slider from '../slider/slider'
import PortfolioPhoto from './__portfolio-photo/__portfolio-photo'

class Portfolio extends React.Component {
  render () {
    // —Ñ–æ—Ä–º–∏—Ä—É–µ–º —Å–ª–∞–π–¥—ã
    let slides = []
    if (Array.isArray(this.props.portfolio) && this.props.portfolio.length > 0) {
      slides = (
        this.props.portfolio.map((item, i) =>
          <PortfolioPhoto
            key={i + item}
            url={item}
            setPopout={this.props.setPopout}
          />
        )
      )
    }

    // –µ—Å–ª–∏ —Å–æ–∞–π–¥–æ–≤ –Ω–µ—Ç —Ç–æ –∏–Ω—Ñ–æ—Ä–º–∏—Ä—É–µ–º
    if (slides.length <= 0) {
      return (
        <div className='container portfolio'>
          <div className='container portfolio__header font-size-24 line-height-36 font-weight-500'>
            <div className='row'>
              <div className='col'>
                <span>–ü–æ—Ä—Ç—Ñ–æ–ª–∏–æ –ø–æ–∫–∞ –ø—É—Å—Ç–æ</span>
              </div>
            </div>
          </div>
        </div>
      )
    }

    return (
      <div className='container portfolio'>
        <div className='container portfolio__header font-size-24 line-height-36 font-weight-500'>
          <div className='row'>
            <div className='col'>
              <span>–ü–æ—Ä—Ç—Ñ–æ–ª–∏–æ</span>
            </div>
          </div>
        </div>

        <Slider
          slidesArray={slides}
        />
      </div>
    )
  }
}
export default Portfolio
