/**
 * –ó–¥–µ—Å—å —è –ø—Ä–æ—Å—Ç–æ —â–∞–º–µ–Ω—è—é —Å–∏–≤–æ–ª—ã –¥–ª—è –∫–æ–¥–∏—Ä–æ–≤–∞–Ω–∏—è
    ENCODING /

    –°–ø–µ—Ü–∏–∞–ª—å–Ω—ã–µ —Å–∏–º–≤–æ–ª—ã:

    –ó–∞—Ä–µ–∑–µ—Ä–≤–∏—Ä–æ–≤–∞–Ω–Ω—ã–µ —Å–∏–º–≤–æ–ª—ã	; , / ? : @ & = + $
    –ù–µ—ç–∫—Ä–∞–Ω–∏—Ä—É–µ–º—ã–µ —Å–∏–º–≤–æ–ª—ã	    –ª–∞—Ç–∏–Ω—Å–∫–∏–µ –±—É–∫–≤—ã, –¥–µ—Å—è—Ç–∏—á–Ω—ã–µ —Ü–∏—Ñ—Ä—ã, - _ . ! ~ * ' ( )
    Score	                    #
 */

function encoding (decodedContent) {
  decodedContent.toString()
  decodedContent = decodedContent
    .replace(/;/g, 'special1')
    .replace(/\//g, 'special3')
    .replace(/\?/g, 'special4')
    .replace(/:/g, 'special5')
    .replace(/@/g, 'special6')
    .replace(/&/g, 'special7')
    .replace(/=/g, 'special8')
    .replace(/\+/g, 'special9')
    .replace(/\$/g, 'specialA0')
    .replace(/\%/g, 'specialA1')
    .replace(/\^/g, 'specialA2')
    .replace(/\"/g, 'specialA3')
    .replace(/‚Ññ/g, 'specialA4')
    .replace(/#/g, 'specialA5')
    .replace(/`/g, 'specialA6')

  return encodeURI(decodedContent)
}

function decoding (encodedContent) {
  encodedContent = decodeURI(encodedContent)
  encodedContent = encodedContent
    .replace(/special1/g, ';')
    .replace(/special2/g, ',')
    .replace(/special3/g, '\/')
    .replace(/special4/g, '?')
    .replace(/special5/g, ':')
    .replace(/special6/g, '@')
    .replace(/special7/g, '&')
    .replace(/special8/g, '=')
    .replace(/special9/g, '+')
    .replace(/specialA0/g, '$')
    .replace(/specialA1/g, '%')
    .replace(/specialA2/g, '^')
    .replace(/specialA3/g, '"')
    .replace(/specialA4/g, '‚Ññ')
    .replace(/specialA5/g, '#')
    .replace(/specialA6/g, '`')

  return encodedContent
}

export default {
  encoding,
  decoding
}
