import React     from 'react';
import './button.css';

class Button extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            id: this.props.id,
            buttonText: this.props.buttonText,
        };
    }


    buttonOnClick = ( elementButton ) => {
        // –∑–∞–ø–æ–º–Ω–∏—Ç—å —Ç–µ–∫—É—â–∏–π —Ç—ç–∫
        let button = elementButton.target;

        // –≤–µ—Ä–Ω—É—Ç—å –∫–æ–ª–ª–±—ç–∫ –µ—Å–ª–∏ –æ–Ω –µ—Å—Ç—å
        if ( this.props.onClick ) {
            this.props.onClick( this.state.id, elementButton );
        }

        // –µ—Å–ª–∏ –Ω–∞ –∫–Ω–≥–æ–ø–∫—É –Ω–∞–∂–∞–ª–∏ 
        if (this.props.isActive) {
            // –ø–æ–º–µ–Ω—è—Ç—å –µ–µ —Å—Ç–∏–ª—å
            button.classList.add('button_theme_inactive');
            // –∏ –≤–µ—Ä–Ω—É—Ç—å –µ–µ —Å—Ç–∏–ª—å —á–µ—Ä–µ–∑ 150 –º–ª —Å–µ–∫
            setTimeout( function () {
                button.classList.remove('button_theme_inactive');
            }, 150);
        }

    };
    // { position: 'absolute', zIndex: 10 }
    render() {
        return (
            <div
                className="button"
                onClick={this.buttonOnClick}
                style={ !this.props.isActive ? { background: '#FBCDCC' } : null }
            >
                { this.state.buttonText }
            </div>
        );
    }
}

export default Button;
